<template>
  <div class="home">
    <div id="main" :style="{ width: '860px', height: '400px' }"></div>
    <div class="homebox">
      <div
        id="generalIncome"
        :style="{ width: '400px', height: '350px' }"
      ></div>
      <div id="cost" :style="{ width: '400px', height: '350px' }"></div>
    </div>
  </div>
</template>
 
<script>
import * as echarts from "echarts";
export default {
  mounted() {
    this.drawLine();
    this.generalIncome();
    this.cost();
  },
  methods: {
    drawLine() {
      var chartDom = document.getElementById("main");
      var myChart = echarts.init(chartDom);
      var option;
      option = {
        title: {
          text: "周销售量",
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: ["玫瑰", "百合", "康乃馨", "洋桔梗", "菊类"],
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        toolbox: {
          feature: {
            saveAsImage: {},
          },
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: [
            "星期一",
            "星期二",
            "星期三",
            "星期四",
            "星期五",
            "星期六",
            "星期天",
          ],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            name: "玫瑰",
            type: "line",
            stack: "Total",
            data: [120, 132, 101, 134, 90, 230, 210],
          },
          {
            name: "百合",
            type: "line",
            stack: "Total",
            data: [220, 182, 191, 234, 290, 330, 310],
          },
          {
            name: "康乃馨",
            type: "line",
            stack: "Total",
            data: [150, 232, 201, 154, 190, 330, 410],
          },
          {
            name: "洋桔梗",
            type: "line",
            stack: "Total",
            data: [320, 332, 301, 334, 390, 330, 320],
          },
          {
            name: "菊类",
            type: "line",
            stack: "Total",
            data: [820, 932, 901, 934, 1290, 1330, 1320],
          },
        ],
      };

      option && myChart.setOption(option);
    },
    generalIncome() {
      var chartGen = document.getElementById("generalIncome");
      var genChart = echarts.init(chartGen);
      var option;
      option = {
        title: {
          text: "年总收入",
          left: "center",
        },
        tooltip: {
          trigger: "item",
        },
        legend: {
          orient: "vertical",
          left: "left",
        },
        series: [
          {
            name: "Access From",
            type: "pie",
            radius: "50%",
            data: [
              { value: 1048, name: "玫瑰" },
              { value: 735, name: "百合" },
              { value: 580, name: "康乃馨" },
              { value: 484, name: "洋桔梗" },
              { value: 300, name: "菊类" },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      };
      option && genChart.setOption(option);
    },
    cost() {
      var chartCost = document.getElementById("cost");
      var costChart = echarts.init(chartCost);
      var option;
      option = {
        title: {
          text: "年总成本",
          left: "center",
        },
        xAxis: {
          type: "category",
          data: ["玫瑰", "百合", "康乃馨", "洋桔梗", "菊类"],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: [
              {
                value: 120,
                itemStyle: {
                  color: "#a90000",
                },
              },
              {
                value: 200,
                itemStyle: {
                  color: "rgb(31, 59, 100)",
                },
              },
              {
                value: 150,
                itemStyle: {
                  color: " rgb(2, 124, 100)",
                },
              },
               {
                value: 80,
                itemStyle: {
                  color: "#f9944a",
                },
              },
                {
                value: 70,
                itemStyle: {
                  color: "#2ac06d",
                },
              },
            ],
            type: "bar",
          },
        ],
      };
      option && costChart.setOption(option);
    },
  },
};
</script>
<style scoped>
#main {
  background-color:#fff;
  border-radius: 8px;
  padding: 20px 20px;
  margin-bottom: 20px;
}
#generalIncome {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px 20px;
}
#cost {
  margin-left: 20px;
  background-color: #fff;
  border-radius: 8px;
  padding: 20px 20px;
}
.homebox {
  display: flex;
}
</style>